html {
  width: 100%;
  overflow-x: hidden;
}

.ant-progress-success-bg, .ant-progress-bg {
  background-color: #FB9408 !important;
}

.App {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  overflow-x: hidden;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: black;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.layer1 {
  position: relative;
  width: 100%;
}
.layer2 {
  height: 50vmax;
  width: 100%;
  opacity: .6;
  background-color: black;
  z-index: 100;
  position: absolute;
  top: 0;
}

.title {
  position: absolute;
  color: white;
  top: 2.5em;
  left: 2.5em;
  font-size: 8vmin;
}

@media only screen and (max-width: 1100px) {
  .title {
    margin-top: 5%;
    left: 5%;
    right: 5%;
  }
}

.menuOps {
  display: flex;
  flex-direction: row;
  position: absolute;
  color: white;
  right: 2em;
  top: 3em;
  z-index: 1100;
  margin-right: 3vmax;
}

.op {
  color: white;
  margin-right: 4em;
  z-index: 1100;
}

.iconText {
  font-size: 1vmax;
}

@media only screen and (max-width: 1100px) {
  .menuOps {
    top: 2.5%;
    margin: auto;
  }

  .op {
    margin: 10px;
  }

  .iconText {
    font-size: 1em;
  }
}

.profile {
  width: 450px;
  height: 450px;
}

.profileContainer {
  margin: auto;
  text-align: center;
  display: flex;
  flex-direction: row;
  width: 80%;
}

.profileText {
  text-align: left;
  margin-left: 5vmax;
  width: 40em;
}

.pluses {
  display: flex;
  flex-direction: row;
  position: relative;
  text-align: center;
  margin: 0 auto;
  width: 1200px;
}

.plusesIcon {
  font-size: 15vmin;
  margin-bottom: 15px;
}

.plusContainer {
  margin: 5% 5% auto 5%;
  width: 100%;
}

.contactContainer {
  background-color: black;
  height: 100%;
  width: 100%;
  text-align: center;
}

.contactText {
  color: white;
  font-family: 'Open Sans', sans-serif;;
}

.contactTextTitle {
  color: white;
  margin-right: 4em;
  font-size: 1vmax;
  z-index: 1400;
}

.skillsContainer {
  display: flex;
  flex-direction: row;
  height: 32em;
  width: 100%;
}

.skillWrapper {
  height: 40em;
}

.skillTitle {
  font-weight: bold;
  font-size: 2em;
}

.skillText {
}

.skillsDescription {
  text-align: left;
  font-size: 20px;
  width: 700px;
  margin: 30px 0 0 15%;
}

.progressBarContainer {
  width: 500px;
  margin: 5% 15% 15% 5%;
  float: right;
}

.progressContainer {
  color: black;
  margin: auto;
  margin-top: 10px;
  display: flex;
  flex-direction: row;
}

.skillTag {
  width: 75px;
  background-color: transparent;
}

.timelineContainer {
  width: 70%;
  margin: 0 0 5% 15%;
  text-align: left;
  overflow-x: auto;
}

.timeline {
  display: flex !important;
  flex-direction: row !important;
}

.figureTitle {
  color: white !important;
  filter: drop-shadow(0 0 0.75rem black);
}

.gridWrapper {
  padding-top: 8em;
  background-color: black;
  height: 800px;
  width: 100%;
  -webkit-clip-path: polygon(0% 0%, 100% 0, 100% 84%, 50% 100%, 0 84%, 0 0);
  clip-path: polygon(0% 0%, 100% 0, 100% 84%, 50% 100%, 0 84%, 0 0);
}

.grid {
  position: relative;
  display: flex;
  flex-direction: row;
  clear: both;
  margin: 0 auto;
  padding: 1em 0 4em;
  max-width: 1000px;
  list-style: none;
  text-align: center;
}

.expText {
  width: 70%;
  height: 30%;
  text-align: left;
  border-radius: 5px;
  margin: auto auto 10% auto;
}

.ant-carousel .slick-slide {
  text-align: center;
  width: 100%;
    height: 40vmax;
  line-height: 160px;
  background: #FB9408;
  overflow: hidden;

}

.imgBlockContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.imgBlock {
  display: block;
  width: 75%;
  max-height: 700px;
  margin: 20px;
}

.imgBlockDesc {
  color: white;
  font-size: 1em;
  font-weight: bold;
}

.carouselDescContainer {
  width: 70%;
  height: 30%;
  text-align: left;
  margin: auto auto 10% auto;
}

.carouselDesc {
  text-align: left;
  font-size: 1.5em;

}

@media only screen and (max-width: 1100px) {

  /*.tempWrapperSinceThisLooksUglyOnMobile {*/
    /*display: none;*/
  /*}*/

  .imgBlock {
    max-width: 45%;
    height: auto;
    width: 45%;
    margin: 20px auto -5% auto;
  }

  .contactTextTitle {
    position: absolute;
    top: 24em;
    right: 2vmax;
    margin: 0;
    font-size: 1.8vmax;
  }

  .expText {
    width: 90%;
    margin: 10% 0 10% 5%;
  }

  .showcase {
    display: none;
  }

  .skillText {
    font-size: 16px;
  }

  .skillTitle {
    font-size: 2em;
  }

  .skillWrapper {
    height: 60em;
    margin-bottom: 5%;
    overflow-y: scroll;
    overflow-x: hidden;
  }

  .profileText {
    margin: 0 5% 0 5%;
    width: 90%;
  }
  .skillsContainer {
    display: block;
  }

  .skillsDescription {
    margin: 30px 0 0 5%;
    width: 90%;
  }

  .progressBarContainer {
    float: none;
    width: 90%;
    margin: 30px 0 0 5%;
  }

  .progressContainer {
    margin: 10px 0 0 0;
  }

  .timelineContainer {
    display: none;
  }

  .profileContainer {
    display: block;
    width: 100%;
  }

  .profile {
    height: 40%;
    width: 90%;
    margin-left: 5% !important;
    margin-bottom: 5% !important;
  }

  .gridWrapper {
    padding-top: 5vmax;
    padding-bottom: 35vmax;
    width: 100%;
    height: 100%;
  }

  .grid {
    display: block;
  }
}

/* Common style */
.grid figure {
  position: relative;
  float: left;
  overflow: hidden;
  margin: 10px 1%;
  min-width: 320px;
  max-width: 480px;
  max-height: 360px;
  width: 20%;
  height: auto;
  background: #3085a3;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
}

@media only screen and (max-width: 1100px) {
  .grid figure {
    margin: auto;
    margin-top: 10px;
    float: none;
  }
}

.grid figure img {
  position: relative;
  display: block;
  min-height: 100%;
  max-width: 100%;
  opacity: 0.8;
}

.grid figure figcaption {
  padding: 1em;
  color: #fff !important;
  font-size: 1.25em;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.grid figure figcaption::before,
.grid figure figcaption::after {
  pointer-events: none;
}

.grid figure figcaption,
.grid figure figcaption > a {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Anchor will cover the whole item by default */
.grid figure figcaption > a {
  z-index: 1000;
  text-indent: 200%;
  white-space: nowrap;
  font-size: 0;
  opacity: 0;
}

.grid figure h2 {
  word-spacing: -0.15em;
  font-weight: 300;
}

.grid figure h2 span {
  font-weight: 800;
}

.grid figure h2,
.grid figure p {
  margin: 0;
}

.grid figure p {
  letter-spacing: 1px;
  font-size: 68.5%;
}

figure.effect-apollo {
  background: whitesmoke;
}

figure.effect-apollo img {
  opacity: 0.95;
  -webkit-transition: opacity 0.35s, -webkit-transform 0.35s;
  transition: opacity 0.35s, transform 0.35s;
  -webkit-transform: scale3d(1.05,1.05,1);
  transform: scale3d(1.05,1.05,1);
}

figure.effect-apollo figcaption::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.5);
  content: '';
  -webkit-transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  -webkit-transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,-100%,0);
  transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,-100%,0);
}

figure.effect-apollo p {
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 3em;
  padding: 0 1em;
  max-width: 150px;
  border-right: 4px solid #fff;
  text-align: right;
  opacity: 0;
  -webkit-transition: opacity 0.35s;
  transition: opacity 0.35s;
}

figure.effect-apollo h2 {
  text-align: left;
}

figure.effect-apollo:hover img {
  opacity: 0.6;
  -webkit-transform: scale3d(1,1,1);
  transform: scale3d(1,1,1);
}

figure.effect-apollo:hover figcaption::before {
  -webkit-transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,100%,0);
  transform: scale3d(1.9,1.4,1) rotate3d(0,0,1,45deg) translate3d(0,100%,0);
}

figure.effect-apollo:hover p {
  opacity: 1;
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}
